.Metrics input {
  background: var(--main-bg-color);
  font-size: 14px;
  font-weight: 300;
  width: 100%;
  text-align: center;
  border: 0;
  outline: 0;
  padding: 0;
  margin: 0;
}

.editing .Metrics input {
  border-bottom: 0.5px solid #000;
  height: 30px;
  width: 32px;
}

/* Apple Watch */
@media only screen and (max-width: 319px) {

  .Metrics input {
    font-size: 10px;
  }
}



