.RoutineModal {
  position: fixed;
  bottom: 80px;
  right: 20px;
}

.RoutineModal .content {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: 0 5px 5px -3px rgba(0,0,0,.2), 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0,0,0,.12);
          box-shadow: 0 5px 5px -3px rgba(0,0,0,.2), 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0,0,0,.12);
  position: absolute;
  bottom: 55px;
  right: 30px;
  z-index: 1;
  height: 50vh;
  width: 75vw;
  overflow: auto;
  background-color: #fff;
  border: 1px solid #ddd;
  -webkit-overflow-scrolling: touch;
  -webkit-transform: scale(0.8);
      -ms-transform: scale(0.8);
          transform: scale(0.8);
  opacity: 0;
  /* will-change: transform,opacity; */
  visibility: hidden;
}

.RoutineModal .show {
  opacity: 1;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  -webkit-transition:
    opacity .03s linear,
    -webkit-transform .12s cubic-bezier(0,0,.2,1),
    -webkit-transform .12s cubic-bezier(0,0,.2,1);
  transition:
    opacity .03s linear,
    -webkit-transform .12s cubic-bezier(0,0,.2,1),
    -webkit-transform .12s cubic-bezier(0,0,.2,1);
  -o-transition:
    opacity .03s linear,
    transform .12s cubic-bezier(0,0,.2,1),
    transform .12s cubic-bezier(0,0,.2,1);
  transition:
    opacity .03s linear,
    transform .12s cubic-bezier(0,0,.2,1),
    transform .12s cubic-bezier(0,0,.2,1);
  transition:
    opacity .03s linear,
    transform .12s cubic-bezier(0,0,.2,1),
    transform .12s cubic-bezier(0,0,.2,1),
    -webkit-transform .12s cubic-bezier(0,0,.2,1),
    -webkit-transform .12s cubic-bezier(0,0,.2,1);
  visibility: visible;
}

.RoutineModal .content .close {
  font-size: 22px;
}

.RoutineModal .Fab.mdc-fab {
  position: absolute;
  right: 0;
  bottom: 0;
}

.RoutineModal .exercises .exercise-label, .RoutineModal .exercises h3 {
  border-bottom: 1px #ddd solid;
  padding: 20px;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
}

.RoutineModal .exercises h3 {
  background: #fafafa;
}

.RoutineModal .description {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: grid;
  padding: 20px;
  justify-items: center;
  height: 100%;
}

.RoutineModal .description .message {
  -ms-flex-item-align: end;
      align-self: end;
  margin-bottom: 50px;
}

.RoutineModal .description .link-favorites {
  color: #000;
  border: 1px #000 solid;
  padding: 10px;
  text-decoration: none;
}


